<template>
  <div :class="classes">
    <Banner :bg="bg" :height="200">
      <template slot="header">分&nbsp;&nbsp;&nbsp;&nbsp;享</template>
      <template>寻找让你心动的地图</template>
    </Banner>
    <div class="gtu-container">
      <router-view></router-view>
    </div>  
  </div>
</template>

<script>
  import {imgDir} from '@/utils/staticUrl'
  const prefix = 'gtu-cases'

  export default {
    name: 'cases_frame',
    components: {
      Banner: () => import(/* webpackChunkName: "common" */'@/components/banner')
    },
    data () {
      return {
        bg: `${imgDir}/cases_banner.jpeg`
      }
    },
    computed: {
      classes () {
        return [`${prefix}`]
      }
    }
  }
</script>


