<template>
  <div :class="prefix">
    <MapShare :class="`${prefix}-map`" ></MapShare>
  </div>
</template>

<script>
  const prefix = 'gtu-cases-share'

  export default {
    name: 'case_detail',
    components: {
      MapShare: () => import(/* webpackChunkName:common */'@/components/mapshare'),
    },
    data () {
      return {
        prefix: prefix
      }
    },
    beforeCreate () {
      // var ak = this.$route.params.ak
      var ak = this.$route.query.ak
      // var params = {ak:'YjdhYTBmMThjNjRiNDBhNGJhNzVlYTIwNjQxMTRmNWY'}
      var params = {ak:ak}
      this.$http.get('/pub/shares/shareMap', {params:params}).then((response) => {
        // 响应成功回调
        const res = response.data
        if(!res.flag){
          this.$router.push({path: '/login'})
        }
      }, (response) => {
        // 响应错误回调
        this.$router.push({path: '/login'})
      })
    }
  }
</script>
<style lang="scss">
  $prefix: '.gtu-cases-share';
  $border-color: #dddee1;
  $lighter-color: #999;
  #{$prefix} {
    &-map {
      _height:630px;
      min-height:1530px;
      background: url('/static/images/cases/cases_1.png') center center no-repeat;
    }
  }
</style>



