<template>
  <ul :class="classes">
    <CaseItem v-for="(l, i) in lists"  :key="i">
      <img slot="img" :src="l.img" :id="l.id" @click="ClickToMapDetail" />
      <template slot="info">
        <a><Icon type="person"></Icon>{{l.author}}</a> 
        <span><Icon type="eye"></Icon>{{l.views}}</span>
      </template>
      <template slot="title"><a :id="l.id" href="#" @click="ClickToMapDetail">{{l.title}}</a></template>
    </CaseItem>
  </ul>
</template>

<script>
  import Icon from 'iview/src/components/icon'
  import {casesImgDir} from '@/utils/staticUrl'

  const prefix = 'gtu-cases-list'

  export default {
    props: {
      type: String,
      keyword: String
    },
    components: {
      Icon,
      CaseItem: () => import(/* webpackChunkName:"cases" */'./CaseItem')
    },
    data () {
      return {
        lists: this.getLists()
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    },
    methods: {
      // 根据类型加载数据
      getLists () {
        console.log(this.type)
        console.log(this.keyword)
        return Array(9).fill(0).map(function (val, i) {
          return {
            id: i,
            title: '北京经济型酒店空间分布',
            author: 'weiq',
            views: '92',
            img: `${casesImgDir}/cases_1.png`
          }
        })
      },
      ClickToMapDetail (event) {
        // alert(event.target.id)
        var ak = 'YjdhYTBmMThjNjRiNDBhNGJhNzVlYTIwNjQxMTRmNWY'
        // this.$router.push('/pub/shares/'+event.target.id)
        this.$router.push('/cases/'+event.target.id)
        // this.$router.push({path: '/pub/shares/'+event.target.id+'/share', query: {ak: ak}})
      }
    },
    watch: {
      keyword (val, oldVal) {
        this.lists = this.getLists()
      }
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-cases-list';

  #{$prefix} {
    display: flex;
    flex-wrap: wrap;
  }
</style>


