<template>
  <div :class="prefix">
    <!-- <CaseSearch></CaseSearch> -->
    <div :class="`${prefix}-title-box`">
      <div :class="`${prefix}-title`">背景经济型据点分布</div>
      <div :class="`${prefix}-info`">
        <a><Icon type="person"></Icon>wei</a> 
        <span><Icon type="eye"></Icon>92</span>
        <span><Icon type="clock"></Icon>2018-10-11</span>
      </div>
    </div>
    <p :class="`${prefix}-desc`">
      经济型酒店
    </p>
    <Map2d  :class="`${prefix}-map`" ></Map2d>
    <div :class="`${prefix}-share-url`">
      <div :class="`${prefix}-barcode`">
        <img src="/pub/shares/qr/SqpRM8pYwB8GKzooaPyS1yRsUMpt8eBL" alt="qrcode">
        扫描二维码分享
      </div>
      <div :class="`${prefix}-url`">
        <p>分享链接</p>
        <p><iInput v-model="url" size="small" readonly icon="ios-copy-outline"></iInput></p>
        <p>嵌入到网页</p>
        <p><iInput v-model="iframeUrl" size="small" readonly icon="ios-copy-outline"></iInput></p>
      </div>
    </div>
  </div>
</template>

<script>
  // import CaseSearch from './CaseSearch'
  import Icon from 'iview/src/components/icon'
  import Input from 'iview/src/components/input'
  const prefix = 'gtu-cases-detail'
  export default {
    name: 'case_detail',
    components: {
      // CaseSearch,
      Icon,
      iInput: Input,
      Map2d: () => import(/* webpackChunkName:common */'@/components/map2d'),
    },
    data () {
      return {
        prefix: prefix,
        id: '',
        url: 'http://localhost:8080/apps/SqpRM8pYwB8GKzooaPyS1yRsUMpt8eBL/shares?ak=2987f7fa516c1888f710306686b44270d005e0443429067b8ae16c1076bce1b4',
        iframeUrl:'<iframe width=480 height=300 src="http://localhost:8080/apps/SqpRM8pYwB8GKzooaPyS1yRsUMpt8eBL/shares?ak=2987f7fa516c1888f710306686b44270d005e0443429067b8ae16c1076bce1b4" frameborder=0></iframe>',
        detail: {}
      }
    },
    methods: {
      getDetailById () {
        //alert(this.id)
      }
    },
    mounted () {
      this.id = this.$route.params.id
      this.detail = this.getDetailById(this.id)
    },
    beforeRouteUpdate (to, from, next) {
      this.id = to.params.id;
      this.detail = this.getDetailById(this.id)
    }
  }
</script>
<style lang="scss">
  $prefix: '.gtu-cases-detail';
  $border-color: #dddee1;
  $lighter-color: #999;
  #{$prefix} {
    position: relative;
    padding: 40px 0;

    &-title-box {
      line-height: 36px;
      border-bottom: 1px solid $border-color;
      font-size: 14px;
      color: $lighter-color;
      &:after {
        content: ' ';
        display: table;
        clear: both;
      }
    }
    &-title {
      float: left;
    }
    &-info {
      float: right;
      &>a {
        color: $lighter-color;
      }
      &>*:not(:last-child) {
        margin-right: 5px;
      }
      .ivu-icon {
          margin-right: 3px;
        }
    }
    &-desc {
      margin: 10px 0 30px;
      line-height: 2;
    }
    &-map {
      min-height: 500px;
      background: url('/static/images/cases/cases_1.png') center center no-repeat;
    }
    &-share-url {
      display: flex;
      align-items: center;
      margin-top: 40px;
      font-weight: 500;
    }
    &-barcode {
      margin-right: 20px;
      text-align: center;
      img {
        display: block;
        width: 150px;
        height: 150px;
        margin-bottom: 5px;
      }
    }
    &-url {
      flex: auto;
      p {
        margin: 5px 0;
      }
      .ivu-input {
        width: 100%;
      }
    }
  }
</style>



