<template>
  <div :class="contentClasses">
    <CaseSearch @on-search="handleSearch"></CaseSearch>
    <Tabs size="small" :animated="false" :value="activeTab" @on-click="handleTabsClick">
      <TabPane label="推荐" name="recommendedCases">
        <CasesList :keyword="keyword" type="recommended"></CasesList>
      </TabPane>
      <TabPane label="最新" name="latestCases">
        <CasesList :keyword="keyword" type="latest"></CasesList>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import Tabs from 'iview/src/components/tabs'
  const prefix = 'gtu-cases-content'

  export default {
    components: {
      Tabs,
      TabPane: Tabs.Pane,
      CaseSearch: () => import(/* webpackChunkName:"cases" */ './CaseSearch'),
      CasesList: () => import(/* webpackChunkName:"cases" */ './CasesList')
    },
    data () {
      return {
        activeTab: 'latestCases',
        keyword: ''
      }
    },
    computed: {
      contentClasses () {
        return [`${prefix}`]
      }
    },
    methods: {
      handleTabsClick (name) {
        this.activeTab = name
      },
      handleSearch (keyword) {
        this.keyword = keyword
      }
    }
  }
</script>
<style lang="scss">
  $prefix: '.gtu-cases-content';

  #{$prefix} {
    position: relative;
    padding: 40px 0;
    font-size: 14px;
    .ivu-tabs-tab{
      font-size: 14px;
      &:hover {
        color: green;
      }
      &-active {
        color: #000;
      }
    }
    .ivu-tabs-ink-bar  {
      background-color: #000;
    }
  }
</style>


