<template>
  <li :class="itemClasses">
    <div :class="itemPicClasses">
      <div :class="itemPicWrapClasses">
        <p><slot name="img" ></slot></p>
        <p :class="itemPicInfoClasses"><slot name="info"></slot></p>
      </div>
    </div>
    <div :class="itemTitleClasses"><slot name="title"></slot></div>
  </li>
</template>

<script>
  import Icon from 'iview/src/components/icon'
  const prefix = 'gtu-cases-item'

  export default {
    name: 'case_item',
    components: {
      Icon
    },
    data () {
      return {}
    },
    computed: {
      itemClasses () {
        return `${prefix}`
      },
      itemPicClasses () {
        return `${prefix}-pic`
      },
      itemPicWrapClasses () {
        return `${prefix}-pic-wrap`
      },
      itemPicInfoClasses () {
        return `${prefix}-pic-info`
      },
      itemTitleClasses () {
        return `${prefix}-title`
      }
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-cases-item';
  #{$prefix} {
    width: 33.3%;
    flex-basis: 33.3%;
    padding: 0 15px;
    margin-top: 10px;
    overflow: hidden;
    cursor: pointer;
    &-pic {
      padding: 5px;
      background-color: #F2F2F2;
      &-wrap {
        padding: 10px;
        background-color: #fff;
        img {
          display: block;
          max-width: 100%;
          height: 200px;
        }
      }
      &-info {
        margin-top: 10px;
        text-align: right;
        color: #bbb;
        a {
          color: #bbb;
          margin-right: 5px;
        }
        .ivu-icon {
          margin-right: 3px;
        }
      }
    }
    &-title {
      padding: 10px 0;
    }
  }
</style>


